import $               from 'ZEPTO_PATH';
import VARIABLE        from 'UNIT_PATH/variable';
import header          from 'MODULES_PATH/header.js';
import layout          from 'MODULES_PATH/layout';
import navSlider       from 'MODULES_PATH/navSlider';
import redSlider       from 'MODULES_PATH/redSlider';
import FooterAd        from 'MODULES_PATH/footerAd';
import scrollMock      from 'MODULES_PATH/scrollMock';
import LazyLoad        from 'MODULES_PATH/lazyLoad';
import footBar         from 'MODULES_PATH/footBar';
import lazySuccess     from 'MODULES_PATH/lazySuccess';
import noticeTabFloat  from 'MODULES_PATH/noticeTabFloat';
import notifyMutually  from 'MODULES_PATH/notifyMutually';
import notifyGoTop     from 'MODULES_PATH/notifyGoTop';
import notifyReturnFix from 'MODULES_PATH/notifyReturnFix';

VARIABLE.CONFIG.ANIMA_SCROLL = true;
window.$ = $;

// 页面布局调整
layout();

// 页面滚动模拟
scrollMock();

// 懒加载
LazyLoad({
	loadSucess: function(img) {
		lazySuccess.loadLogoImg(img, '.notify-item');
	}
});

// 场次时间悬浮
noticeTabFloat();

// 开售提醒状态控制
notifyMutually();

// 开售提醒计数功能
notifyGoTop();

// 公共头部
header.headerInit();

// 导航滑动
navSlider.init();

// 轮播图
redSlider.init();

// 尾部广告
var footerAd = new FooterAd();

footerAd.init();

// 底部导航
footBar();

// 记住位置
notifyReturnFix();



