import $ from 'ZEPTO_PATH';
import VARIABLE from 'UNIT_PATH/variable';

function main() {
	if(!$('.notify-item').length) return;
	VARIABLE.CONFIG.ANIMA_SCROLL ? _scrollMock() : _winScroll();
}

function _winScroll() {
	var go_top_btn = $('#go-top');
	var scrollTop, wHeight;

	go_top_btn.click(function() {
		$(window).scrollTop(0);
	});

	$(window).bind('scroll', function() {
		scrollTop = $(window).scrollTop();
		wHeight = $(window).height();

		if(scrollTop >= wHeight){
			go_top_btn.removeClass('none');
		}else{
			go_top_btn.addClass('none');
		}
	});
}

function _scrollMock() {
	var view_scroll    = $('#scroller');
	var items          = $('.notify-item a');
	var count_text     = $('#count');
	var count_now      = $('#now');
	var back_text      = $('#back');
	var total_text     = $('#total');
	var jd_footer      = $('#jd-footer');
	var red_logo       = $('#red_logo');
	var actItemPosArr = [];
	var tabItemsArr = {};
	var tabLastActive = '';
	var notifyItem = $('.notify-item');
	var go_top_btn = $('#go-top');
	var noticeTab = $('.notice-tab');
	var notifyIndex = 0;
	var tabIndex = 1;
	var item_count_inview, wrapper_height, scroll_height, item_height, first_item_top, scroll_view_top, y;
	var max_item_count_inview = Math.ceil(items.length / 2);
	var _SCROLL = VARIABLE.GLOBAL.SCROLL_OBJ;

	if(sessionStorage.getItem('notifyIndex')) {
		let _notifyIndex = parseInt(sessionStorage.getItem('notifyIndex'), 10);

		if(!isNaN(_notifyIndex)) {
			notifyIndex = _notifyIndex;
		};
		
		sessionStorage.removeItem('notifyIndex');
	}

	// 填充活动总数
	total_text.text(items.length);

	// 回到顶部
	go_top_btn.click(function() {
		if(go_top_btn.find('.back').hasClass('none')) {
			return;
		}
		_SCROLL.scrollTo(0, true);
		go_top_btn.addClass('none');
		$('#float-bar').addClass('none');
	});

	var itemMax = 0;
	$('.notify-item').each(function(i, v) {
		var _itemWrapIndex = $('.notify-wrap').index($(this).closest('.notify-wrap'));
		var type = $(this).closest('.notify-wrap').attr('type');

		tabItemsArr[i + 1] = type;
	});

	// 滚动之前(手指已经与屏幕接触)
	_SCROLL.addScrollstartHandler(function() {
		var y = _SCROLL.getScrollTop();

		actItemPosArr.length = 0;
		notifyIndex = 0;
		wrapper_height  = _SCROLL.getViewHeight() - $('#footer-nav').height();
		// 记录每个活动坐标
		notifyItem.each(function(i, v) {
			var _this = $(this);

			actItemPosArr.push({
				top: _this.offset().top - view_scroll.offset().top + _this.height(),
				index: i
			});

			if(actItemPosArr[i] && y >= actItemPosArr[i].top - wrapper_height) {
				notifyIndex = i + 1;
			}
		});
	});

	var num_last = 0;

	// 滚动时
	_SCROLL.addScrollingHandler(function() {
		var y = _SCROLL.getScrollTop();

		// 计数逻辑
		if(actItemPosArr[notifyIndex] && y >= actItemPosArr[notifyIndex].top - wrapper_height) {
			notifyIndex++;
			if(actItemPosArr[notifyIndex] && actItemPosArr[notifyIndex - 1] && actItemPosArr[notifyIndex].top == actItemPosArr[notifyIndex - 1].top) {
				notifyIndex++;
			}
		}
		
		var preIndex = notifyIndex - 1;

		if(actItemPosArr[preIndex] && y <= actItemPosArr[preIndex].top - wrapper_height) {
			notifyIndex--;
			if(actItemPosArr[notifyIndex] && actItemPosArr[notifyIndex - 1] && actItemPosArr[notifyIndex].top == actItemPosArr[notifyIndex - 1].top) {
				notifyIndex--;
			}
		}

		if(notifyIndex >= 8) {
			go_top_btn.removeClass('none');
			if(notifyIndex != num_last){
				count_now.text(notifyIndex);
				num_last = notifyIndex;
			}
		}else{
			go_top_btn.addClass('none');
		}

		if(tabLastActive != tabItemsArr[notifyIndex] && typeof tabItemsArr[notifyIndex] != 'undefined' && !VARIABLE.GLOBAL.NOTIFY_LIST.clickTabBtn) {
			tabLastActive = tabItemsArr[notifyIndex];
			$('.notice-tab li').removeClass('active');
			noticeTab.find('li[type="'+ tabLastActive +'"]').addClass('active');
		}

		// 显示计数器
		count_text.removeClass('none');
		back_text.addClass('none');
	});

	// 滚动停止时
	_SCROLL.addScrollendHandler(function() {
		VARIABLE.GLOBAL.NOTIFY_LIST.clickTabBtn = false;

		if(notifyIndex >= 8) {
			go_top_btn.removeClass('none');
		}else{
			go_top_btn.addClass('none');
		}

		// 隐藏计数器
		count_text.addClass('none');
		back_text.removeClass('none');
	});
}

module.exports = main;


