import $ from 'ZEPTO_PATH';
import VARIABLE from 'UNIT_PATH/variable';
var _SCROLL;

function main() {
	if(!VARIABLE.CONFIG.ANIMA_SCROLL || !$('.notify-item').length) return;

	_SCROLL = VARIABLE.GLOBAL.SCROLL_OBJ;
	tabFloat();
	tabLayout();
	tabEventBind();
}

function tabLayout() {
	var allLi = $('.notice-tab').eq(0).find('li');

	if(allLi.length <= 1) {
		$('.notice-tab').remove();
		_SCROLL.refresh();
	}

	var liWidth = allLi.width();
	var scroll = $('#scroller');

	if(allLi.length * liWidth < scroll.width()) {
		$('.notice-tab li').css('width', 100 / allLi.length + '%');
	}

	$('.notice-tab').removeClass('op0');
}

function tabEventBind() {
	var titleArr = $('.notice-start-time');
	var viewScroll = $('#scroller');

	$('.notice-tab li').bind('tap', function() {
		var clickIndex = $(this).index();
		var type = $(this).attr('type');
		var title = $('.notice-start-time[type="'+ type +'"]');

		VARIABLE.GLOBAL.NOTIFY_LIST.clickTabBtn = true;
		$('.notice-tab li').removeClass('active');
		$('.notice-tab').each(function() {
			$(this).find('li').eq(clickIndex).addClass('active');
		});
		if(!title.length)return;

		var top = title.offset().top - viewScroll.offset().top;
		var marginTop = parseFloat(titleArr.css('margin-top'));

		_SCROLL.scrollTo(top - $('.notice-tab').not('.float-bar').height() - marginTop, true);
	});
}

function tabFloat() {
	var viewScroll = $('#scroller');
	var tabTop = $('.notice-tab').not('.float-notice').offset().top - viewScroll.offset().top;
	var floatBar = $('#float-notice');

	_SCROLL.addScrollingHandler(function() {
		var y = _SCROLL.getScrollTop();

		if(y >= tabTop) {
			floatBar.removeClass('none');
		}else{
			floatBar.addClass('none');
		}
	});
}

module.exports = main;


