import $ from 'ZEPTO_PATH';
var VARIABLE = require('UNIT_PATH/variable');
var _Scroll;

function main() {
	if(!window.sessionStorage) {
		return;
	}

	var _Lazy = VARIABLE.GLOBAL.LAZY;

	_Scroll = VARIABLE.GLOBAL.SCROLL_OBJ;

	if(sessionStorage.getItem('notify_y')) {
		if(VARIABLE.CONFIG.ANIMA_SCROLL) {
			_Scroll.scrollTo(parseInt(sessionStorage.getItem('notify_y'), 10));
			_Lazy.refreshImg();
		}else{
			$(window).scrollTop(parseInt(sessionStorage.getItem('notify_y'), 10));
		}
		sessionStorage.removeItem('notify_y');
	};

	bindClick();
}

function bindClick() {
	$('.notify-wrap').bind('click', function(ev) {
		var target = $(ev.target);
		var _a = target.closest('a');

		if(!_a.length) return;

		var _href = _a.attr('href');

		if(_href == '' || _href == 'javascript:;' || _href == '#') return;

		if(VARIABLE.CONFIG.ANIMA_SCROLL) {
			window.sessionStorage.setItem('notify_y', _Scroll.getScrollTop());
			window.sessionStorage.setItem('notifyIndex', $('#now').text());
		}else{
			window.sessionStorage.setItem('notify_y', $(window).scrollTop());
		}
	});
}

module.exports = main;


